export function rechargeFilter(v) {
  return v === 2 ? '' : ''
}

export function widthdrawFilter(v) {
  return v === 0 ? '' : v === 1 ? '' : v === 2 ? '' : ''
}
export function statusFilter(v) {
  if (v.retire === 1) {
    return ''
  } else {
    if (v.orderStatus === 9) {
      return ''
    } else if (v.sendToken === 1) {
      return ''
    } else {
      return ''
    }
  }
}



